/* 페이지별 개별 margin */
.content.sys__commoncode .input__wrap .section__inner > ul > li:nth-of-type(3),
.content.group__code .input__wrap .section__inner > ul > li:nth-of-type(3) {
  margin-top: 0px;
}

.content.admin__commoncode__manage
  .input__wrap
  .section__inner
  > ul.selector__wrap
  > li:first-of-type,
.content.project__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.system__admin .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.companies__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.depart__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.menu__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.func__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.auth__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.sys__groupcode__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
.content.admin__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type {
  margin-bottom: 0px !important;
}

/* // 페이지별 개별 margin */
.content.batteries__add__update .sub__nav,
.content.batteries__add__update > .page__title__wrap {
  grid-column: span 1 / span 1;
}

.section__grid__wrap.batteries__add__update {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.content.batteries__add__update .sub__nav .right button:first-of-type {
  margin-right: 16px;
}

.content.batteries__add__update .input__wrap .section__inner > ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.content.batteries__add__update
  .input__wrap
  .section__inner
  > ul.selector__wrap
  > li:first-of-type {
  margin-bottom: 24px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li {
  width: 49%;
  margin-top: 24px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li.w100 {
  width: 100%;
}
.content.batteries__add__update .input__wrap .section__inner > ul li dd.address__search__list {
  display: flex;
  align-items: center;
}

.content.batteries__add__update
  .input__wrap
  .section__inner
  > ul
  > li
  dd.address__search__list
  > input {
  margin-right: 22px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li.w50 {
  width: 49% !important;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li textarea {
  height: 96px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li:first-of-type,
.content.batteries__add__update .input__wrap .section__inner > ul > li:nth-of-type(2) {
  margin-top: 0px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li dl dt {
  margin-bottom: 8px;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li dl dd input,
.content.batteries__add__update .input__wrap .section__inner > ul > li dl dd select {
  width: 100%;
}

.content.batteries__add__update
  .input__wrap
  .section__inner
  > ul
  > li
  dl
  dd
  .react-datepicker__input-container
  > input {
  max-width: none;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li:last-of-type {
  width: 100%;
}

.content.batteries__add__update .input__wrap .section__inner > ul > li:last-of-type textarea {
  height: 96px;
}

@media (max-width: 650px) {
  /* 페이지별 개별 margin */
  .content.sys__commoncode .input__wrap .section__inner > ul > li:nth-of-type(3),
  .content.group__code .input__wrap .section__inner > ul > li:nth-of-type(3) {
    margin-top: 24px;
  }

  .content.admin__commoncode__manage
    .input__wrap
    .section__inner
    > ul.selector__wrap
    > li:first-of-type,
  .content.depart__manage .project__manage .section__inner > ul.selector__wrap > li:first-of-type,
  .content.depart__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.system__admin .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.companies__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.menu__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.func__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.auth__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type,
  .content.sys__groupcode__manage
    .input__wrap
    .section__inner
    > ul.selector__wrap
    > li:first-of-type,
  .content.admin__manage .input__wrap .section__inner > ul.selector__wrap > li:first-of-type {
    margin-bottom: 24px !important;
  }

  /* // 페이지별 개별 margin */
  .content.batteries__add__update .input__wrap .section__inner > ul {
    flex-direction: column;
  }

  .content.batteries__add__update .input__wrap .section__inner > ul > li {
    width: 100%;
  }

  .content.batteries__add__update .sub__nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .content.batteries__add__update .sub__nav .left {
    margin-bottom: 24px;
  }

  .content.batteries__add__update .input__wrap .section__inner > ul > li.w50 {
    width: 100% !important;
  }
}
